button {
  border-radius: 6px;
  border: 3px solid var(--white);
  box-shadow: 0 0 6px 0px var(--shadow);
  color: var(--white);
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  height: 72px;
  width: 240px;

  &:not([disabled]) {
    &:hover {
      border-color: var(--black);
      color: var(--black);
      transition: 0.2s all ease-in-out;
    }
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
